/*
Theme Name: Pixietubeuse
Author: Jérémy Young & Sandrine Martinez
Description: Thème personnalisé pour le site Pixietubeuse
Version: 1.0
*/
body {
  margin: 0;
  font-family: 'Calibri', 'Arial';
  font-size: 16px;
  color: #024269; }

header .content {
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    header .content {
      width: 1000px; } }

.header-link-home {
  display: block; }

#header-custom-image {
  display: block;
  width: 100%;
  height: auto; }

nav {
  margin-bottom: 20px;
  border-width: 0px 0px 3px 0px;
  border-style: dotted;
  border-color: #AA357A; }

nav .content {
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    nav .content {
      width: 1000px; } }
  nav .content::after {
    clear: both;
    content: "";
    display: block; }

nav .menu {
  margin: 0;
  padding: 15px 0px;
  float: left; }
  nav .menu::after {
    clear: both;
    content: "";
    display: block; }

nav .menu .menu-item {
  padding-right: 30px;
  float: left;
  list-style: none;
  font-family: "Satisfy", cursive;
  font-size: 24px;
  color: #024269;
  background-image: url("images/menu_bg.svg");
  background-size: 100% 40px;
  background-repeat: no-repeat; }

nav .menu .menu-item:hover {
  background-image: url("images/menu_bg_hover.svg"); }

nav .menu .menu-item a {
  color: #024269;
  text-decoration: none; }

nav .menu .menu-item-home .dashicons-admin-home::before {
  font-size: 32px; }

nav .sub-menu {
  position: absolute;
  display: none;
  width: 650px;
  padding: 10px 0;
  background-color: #ffffff;
  border-width: 0 3px 3px 3px;
  border-style: dotted;
  border-color: #AA357A;
  z-index: 1000; }

nav .sub-menu .menu-item {
  display: block;
  float: none;
  padding: 5px 10px;
  background-image: none;
  font-family: 'Calibri', 'Arial';
  font-size: 16px; }

nav .sub-menu .menu-item:hover {
  background-color: #FAEDF3;
  background-image: none; }

nav .sub-menu .menu-item:hover a {
  display: block;
  font-weight: bold;
  color: #AA357A; }

nav .boutons-menu {
  float: right; }

#pixie-sidebar {
  padding: 7px 10px;
  background-color: #E8E9ED;
  border-radius: 3px; }
  @media screen and (min-width: 1200px) {
    #pixie-sidebar {
      float: left;
      width: 230px;
      margin-right: 30px; } }

#bienvenue .bienvenue-image {
  display: block;
  width: 200px;
  margin: 0 auto;
  border-radius: 50%; }

#bienvenue h2 {
  font-family: "Satisfy", cursive;
  font-size: 22px;
  text-align: center; }

#bienvenue .bienvenue-signature {
  display: block;
  width: 200px;
  margin: 0 auto; }

#pixie-sidebar .items {
  margin-top: 20px;
  padding: 10px;
  background-color: #ffffff;
  border-radius: 3px; }

#pixie-sidebar .contact-information,
#pixie-sidebar .presentation-information {
  float: left;
  width: 50%; }

#pixie-sidebar .more-informations {
  padding: 30px 0 0 0; }

#pixie-sidebar .more-informations:after {
  display: block;
  content: '';
  clear: both; }

#pixie-sidebar .contact-information a,
#pixie-sidebar .presentation-information a {
  display: block;
  text-align: center;
  text-decoration: none;
  color: #E7A6C5; }

#pixie-sidebar .contact-information a:hover,
#pixie-sidebar .presentation-information a:hover {
  color: #AA357A; }

#pixie-sidebar .contact-information img,
#pixie-sidebar .presentation-information img {
  width: 40px; }

@media screen and (min-width: 1200px) {
  .posts-list {
    float: left;
    width: 720px; } }

.post-preview {
  margin-bottom: 20px;
  border-radius: 3px;
  border-color: #024269;
  border-style: solid;
  border-width: 1px; }

.post-preview-image {
  height: 250px;
  overflow: hidden;
  border-radius: 3px 3px 0px 0px; }

.post-preview-image img {
  position: relative;
  top: -25%;
  width: 100%;
  height: auto; }

.post-preview-resume {
  padding: 10px 15px; }

.post-preview-resume h2 {
  margin: 0;
  font-family: "Satisfy", cursive;
  font-size: 33px;
  color: #024269;
  text-align: center; }

.post-preview a.more-link {
  text-decoration: none; }

.post-preview a.more-link {
  display: block;
  margin: 0 auto;
  width: 150px;
  content: "Lire la suite";
  padding: 5px 20px;
  font-family: "Satisfy", cursive;
  font-size: 22px;
  text-align: center;
  color: #024269;
  background-color: #E8E9ED;
  border-radius: 3px; }

.post-preview-date-parution {
  font-family: 'Calibri', 'Arial';
  font-size: 13px;
  font-style: italic;
  text-align: center;
  color: #E7A6C5; }

footer {
  margin: 30px 0px 0px 0px;
  padding: 30px 0px;
  border-width: 3px 0px 0px 0px;
  border-style: dotted;
  border-color: #AA357A; }

footer .content {
  text-align: center;
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    footer .content {
      width: 1000px; } }

footer .signature-footer {
  width: 500px; }

[id^=menu-reseaux-sociaux] ul {
  margin: 0;
  padding: 0; }

[id^=menu-reseaux-sociaux] li {
  margin-right: 10px;
  padding-right: 0px !important;
  display: inline-block;
  list-style: none;
  background-image: none !important; }

[id^=menu-reseaux-sociaux] img {
  margin-top: 5px;
  height: 25px; }

.reseaux-partage {
  padding-top: 5px;
  line-height: 48px; }

.reseaux-partage a {
  display: inline-block; }

.reseaux-partage img {
  display: block;
  height: 25px; }

#pixie-comments .write-comment {
  padding: 30px 0;
  background-color: #E8E9ED; }

#pixie-comments .write-comment h3 {
  margin: 0 0 30px 0;
  font-family: "Satisfy", cursive;
  font-size: 33px;
  color: #AA357A; }

#pixie-comments .write-comment h3#reply-title a {
  color: #E7A6C5;
  text-decoration: none; }

#pixie-comments .write-comment h3#reply-title small a {
  float: right;
  margin-right: 60px;
  font-family: 'Calibri', 'Arial';
  font-size: 16px;
  text-decoration: none;
  color: #AA357A; }

#pixie-comments .write-comment label {
  display: inline-block;
  width: 150px;
  font-family: 'Calibri', 'Arial';
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  vertical-align: top; }

#pixie-comments .write-comment input,
#pixie-comments .write-comment textarea {
  font-family: 'Calibri', 'Arial';
  font-size: 16px;
  border-width: 0px;
  border-radius: 3px;
  padding: 5px 10px; }

#pixie-comments .write-comment input:focus,
#pixie-comments .write-comment textarea:focus {
  outline: none; }

#pixie-comments .write-comment .comment-form-author,
#pixie-comments .write-comment .comment-form-email {
  display: inline-block;
  width: 49%; }

#pixie-comments .write-comment .comment-form-author,
#pixie-comments .write-comment .comment-form-email,
#pixie-comments .write-comment .comment-form-comment {
  margin-bottom: 20px; }

#pixie-comments .write-comment .form-submit {
  padding-left: 150px; }

#pixie-comments .write-comment .comment-form-author input,
#pixie-comments .write-comment .comment-form-email input {
  width: 280px;
  margin-left: -2px; }

#pixie-comments .write-comment .comment-form-comment textarea {
  width: 775px;
  height: 50px; }

#pixie-comments .write-comment .form-submit input {
  width: 795px;
  padding: 0px 10px;
  font-family: "Satisfy", cursive;
  font-size: 22px;
  color: #ffffff;
  background-color: #AA357A;
  cursor: pointer; }

#pixie-comments .write-comment .logged-in-as {
  margin-bottom: 30px; }
  #pixie-comments .write-comment .logged-in-as::after {
    clear: both;
    content: "";
    display: block; }

#pixie-comments .write-comment .logged-in-as .link-identity {
  color: #C472A2;
  font-family: "Satisfy", cursive;
  font-size: 18px;
  text-decoration: none; }

#pixie-comments .write-comment .logged-in-as .link-deconnexion {
  float: right;
  margin-right: 60px;
  font-weight: bold;
  text-decoration: none;
  color: #AA357A; }

#pixie-comments .read-comments {
  margin: 30px 0; }

#pixie-comments .read-comments .texte-comments {
  font-family: "Satisfy", cursive;
  font-size: 33px;
  color: #E7A6C5; }

#pixie-comments .read-comments ul {
  margin: 0;
  padding: 0; }

#pixie-comments .read-comments li {
  margin-top: 20px;
  list-style: none;
  border-style: solid;
  border-width: 1px;
  border-color: #024269;
  border-radius: 3px; }

#pixie-comments .read-comments ul.children {
  padding: 0 30px;
  background-color: #FBFBFC; }

#pixie-comments .read-comments ul.children li {
  margin-top: 0;
  background-color: #ffffff;
  border-width: 2px 0 0 0;
  border-style: dotted; }

#pixie-comments .read-comments .comment-body {
  position: relative; }

#pixie-comments .read-comments .comment-body p {
  padding: 0 55px; }

#pixie-comments .read-comments .comment-author {
  padding: 10px;
  background-color: #E8E9ED; }

#pixie-comments .read-comments .comment-author img {
  margin-right: 10px;
  border-radius: 50%;
  vertical-align: middle; }

#pixie-comments .read-comments .comment-author cite {
  font-weight: bold;
  font-style: normal;
  text-transform: uppercase;
  vertical-align: middle; }

#pixie-comments .read-comments .comment-meta {
  position: absolute;
  top: 15px;
  right: 10px; }

#pixie-comments .read-comments .comment-meta .comment-edit-link {
  color: #C472A2; }

#pixie-comments .read-comments .comment-meta a {
  font-weight: bold;
  text-decoration: none;
  color: #ffffff; }

#pixie-comments .read-comments .reply a {
  display: block;
  padding: 5px 10px;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  color: #024269;
  background-color: #FBFBFC; }

#pixie-comments .read-comments .says {
  color: #E8E9ED; }

#pixie-comments .pagination-comments {
  margin-top: 30px;
  font-size: 20px;
  text-align: center;
  line-height: 40px; }
  #pixie-comments .pagination-comments::after {
    clear: both;
    content: "";
    display: block; }

#pixie-comments .pagination-comments a {
  text-decoration: none;
  color: #E7A6C5; }

#pixie-comments .pagination-comments .current {
  font-weight: bold;
  color: #AA357A; }

#pixie-comments .pagination-comments .prev {
  float: left; }

#pixie-comments .pagination-comments .next {
  float: right; }

#pixie-comments .pagination-comments .prev,
#pixie-comments .pagination-comments .next {
  width: 40px;
  height: 40px; }

#pixie-comments .pagination-comments .prev .pagination-prev,
#pixie-comments .pagination-comments .next .pagination-next {
  width: 40px;
  height: 40px;
  background-color: #ffffff;
  background-repeat: no-repeat;
  border-radius: 50%; }

#pixie-comments .pagination-comments .prev .pagination-prev:hover,
#pixie-comments .pagination-comments .next .pagination-next:hover {
  background-color: #C472A2; }

#pixie-comments .pagination-comments .prev .pagination-prev {
  background-image: url("images/btn_arrow_left.svg");
  background-position: 30%; }

#pixie-comments .pagination-comments .prev .pagination-prev:hover {
  background-image: url("images/btn_arrow_left_hover.svg"); }

#pixie-comments .pagination-comments .next .pagination-next {
  background-image: url("images/btn_arrow_right.svg");
  background-position: 70%; }

#pixie-comments .pagination-comments .next .pagination-next:hover {
  background-image: url("images/btn_arrow_right_hover.svg"); }

#pixie-index .content,
#pixie-single .content,
#pixie-comments .write-comment .content,
#pixie-comments .read-comments .content,
#pixie-page .content,
#pixie-404 .content {
  margin: 0 auto !important; }
  @media screen and (min-width: 1200px) {
    #pixie-index .content,
    #pixie-single .content,
    #pixie-comments .write-comment .content,
    #pixie-comments .read-comments .content,
    #pixie-page .content,
    #pixie-404 .content {
      width: 1000px; } }
  #pixie-index .content::after,
  #pixie-single .content::after,
  #pixie-comments .write-comment .content::after,
  #pixie-comments .read-comments .content::after,
  #pixie-page .content::after,
  #pixie-404 .content::after {
    clear: both;
    content: "";
    display: block; }

#pixie-single .content .article-detail img,
#pixie-page .content .article-detail img {
  display: block;
  max-width: 100%;
  height: auto; }

#pixie-page {
  margin-bottom: 30px; }

#pixie-404 h2,
#pixie-page .page-detail h2 {
  margin: 0;
  font-family: "Satisfy", cursive;
  font-size: 33px;
  text-align: center; }

#pixie-single .article-detail {
  margin: 30px 0; }

#pixie-single .article-detail h2 {
  margin: 0;
  font-family: "Satisfy", cursive;
  font-size: 33px;
  text-align: center; }

#pixie-single .article-detail .article-date-parution {
  font-style: italic;
  text-align: center;
  color: #E7A6C5; }

#pixie-single .article-detail .article-image {
  padding: 30px 0; }

#pixie-single .article-detail .article-image img {
  width: 100%;
  height: auto; }

#pixie-single .article-partage {
  margin: 60px 0; }

#pixie-single .article-partage:after {
  display: block;
  content: "";
  clear: both; }

#pixie-single .article-partage .center-partage {
  display: block;
  margin: 0 auto;
  width: 450px; }

#pixie-single .article-partage .texte-partage {
  float: left;
  font-family: "Satisfy", cursive;
  font-size: 33px;
  color: #E7A6C5; }
